import { addArticle } from '../../api/article';

export default {
  name: 'editor',
  middleware: 'authenticate',
  data(){
    return {
      title: '',
      description: '',
      body: '',
      tagList: ''
    }
  },
  methods: {
    async addArticle(){
      const {data} = await addArticle({
        article: {
          title: this.title,
          description: this.description,
          body: this.body,
          tagList: JSON.stringify(this.tagList.split(',')),
        }
      })
      this.$router.push(`/article/${data.article.slug}`)
    }
  }
};
